import {NgModule, Component} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {HomepageComponent} from './homepage/homepage.component';
import {LayoutComponent} from './layout/layout.component';
import {ContactComponent} from './contact/contact.component';
import {BlogsComponent} from './blog-section/blogs/blogs.component';
import {Blogs2Component} from './blog-section/blogs2/blogs2.component';
import { AboutComponent } from './about/about.component';


const routes: Routes = [
  {
    path: '', redirectTo: 'home', pathMatch: 'full'
  },
  {
    path: 'home', component: HomepageComponent
  },
  {
    path: '',
    component: LayoutComponent,
    children: [
      {
        path: 'business-service-management',
        loadChildren: './business-service-management/business-service-management.module#BusinessServiceManagementModule'
      },
      {
        path: 'information-management',
        loadChildren: './infmanagement/infmanagement.module#InfmanagementModule'
      },
      {
        path: 'infrastructure-and-operations',
        loadChildren: './infrastructure-and-management/infrastructure-and-management.module#InfrastructureandManagementModule'
      },
      {
        path: 'it-risk-and-security',
        loadChildren: './it-risk-security/it-risk-security.module#ItRiskandSecurityModule'
      },
      {
        path: 'application-management',
        loadChildren: './application-management/application-management.module#ApplicationManagementModule'
      },
      {
        path: 'data-science',
        loadChildren: './data/data.module#DataModule'
      },
      {
        path: 'contact',
        component:ContactComponent

      },
      {
        path: 'about',
        component:AboutComponent

      },
      {
        path: 'blogs',
        component:BlogsComponent
      },
      {
        path: 'blogs2',
        component:Blogs2Component
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {
    anchorScrolling: 'enabled',
    onSameUrlNavigation: 'reload',
    scrollPositionRestoration: 'enabled'
  })],
  exports: [RouterModule]
})
export class AppRoutingModule {
}